<template>
<div class="userView" >
    <header>
        <h1>Usuarios</h1>
    </header>
    <main class="container" >
        <section>
            <FormUsers />
        </section>

        <section>
            <TableUsers :usuarios="usuarios" />
        </section>
        

    </main>
    

</div>

</template>

<script>

import FormUsers from "@/components/FormUsers.vue";
import TableUsers from "../components/TableUsers.vue";
import { mapState, mapActions } from 'vuex';

export default{
    name: "UsersView",
    components: {
        TableUsers,
        FormUsers
    },
    computed: {
        ...mapState(["usuarios"])
    },
    methods: {
        ...mapActions(["setUsers"])

    },
    mounted(){
        this.setUsers();
    }
}

</script>