<template>
<div  class="tableUsers" >

    <section>

        <table class="table">
  <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">Nombre</th>
      <th scope="col">Email</th>
      <th scope="col">Acciones</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(usuario, index) in usuarios" :key="usuario.id" >
      <th scope="row">{{ index + 1 }}</th>
      <td>{{ usuario.nombre }}</td>
      <td>{{ usuario.email }}</td>
      <td>
        <div>
            <button class="btn btn-warning m-1" @click="preUpdateUser(usuario.id)" data-bs-toggle="modal"
            data-bs-target="#updateModal" >Actualizar</button>
            <button class="btn btn-danger m-1" @click="delUser(usuario.id)">Eliminar</button>
        </div>
      </td>
    </tr>
 
   
  </tbody>
</table>

    </section>

</div>

<!-- Modal actualizar -->

<div class="modal fade" id="updateModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1"
    aria-labelledby="updateModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="updateModalLabel">Editar datos de usuario</h1>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          
          <form @submit.prevent="submitUpdate">
            <div class="py-2">
              <label for="" class="form-label">ID:</label>
              <input type="text" class="form-control" name="id" v-model="editUser.id" disabled>
            </div>
            <div class="py-2">
              <label for="" class="form-label">Nombre:</label>
              <input type="text" class="form-control" name="nombre" v-model="editUser.nombre">
            </div>
            <div class="py-2">
              <label for="" class="form-label">Email:</label>
              <input type="text" class="form-control" name="email" v-model="editUser.email">
            </div>
            <div class="py-2">
              <input type="submit" value="Actualizar usuario" class="btn btn-success">
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
import { mapActions } from 'vuex'

export default{
    name: "TableUsers",
    data(){
        return{
            updateModal: '',
            editUser: {
                id: '',
                nombre: '',
                email: ''
            }
        }
    },
    props: {
        usuarios: Array
    },
    methods: {
        ...mapActions(['delUser', 'updateUser']),
        preUpdateUser(userID) {
            let { id, nombre, email } = this.usuarios.find(usuario => usuario.id == userID);
            this.editUser = { id, nombre, email };
        },
        submitUpdate() {
            this.updateUser(this.editUser);
            this.editUser = { id: '', nombre: '', email: '' };
            this.updateModal.hide();
        }
    },
    mounted(){
        this.updateModal = new window.bootstrap.Modal(document.getElementById('updateModal'));
    }
}
</script>

<style scoped lang="scss">

</style>