<template>
<div class="formUsers" >

    <div class="row justify-content-center">
            <div class="col-12 col-md-6">
                <h3 class="py-3 text-center">Agregar usuario</h3>
                <form @submit.prevent="addNewUser">
                    <div class="mb-2">
                        <label for="nombre">Nombre</label>
                        <input type="text" class="form-control" id="nombre" v-model="nuevoUsuario.nombre" required>
                        
                    </div>
                    <div class="mb-2">
                        <label for="email">Email</label>
                        <input type="email" class="form-control" id="email" v-model="nuevoUsuario.email" required>
                       
                    </div>
                
                    <div class="form-floating mb-2 text-end">
                        <button class="btn btn-primary">Agregar</button>
                    </div>
                </form>
            </div>
        </div>

</div>

</template>

<script>
import { mapActions } from 'vuex'
export default{
    name: "FormUsers",
    data(){
        return{
            nuevoUsuario: {
                nombre: "",
                email: ""
            }

        }
    },
    methods: {
        ...mapActions(["addUser"]),
        async addNewUser() {
            let usuario = {
                nombre: this.nuevoUsuario.nombre,
                email: this.nuevoUsuario.email,
                
            }
            await this.addUser(usuario)
            this.nuevoUsuario.nombre = '';
            this.nuevoUsuario.email = ''
            
            alert("Usuario agregado correctamente.");
        },
    }
}

</script>

